import styles from "./Detail.module.css";
import { BiShoppingBag } from "react-icons/bi";
import { FaBars } from "react-icons/fa";
import { IoCloseOutline } from "react-icons/io5";
import {useNavigate, useParams } from 'react-router-dom';
import axios from 'axios';
import { useEffect, useState } from 'react';
import { addItem, setProductDetail } from "../store";
import { useDispatch } from "react-redux";

function Detail({product, perfume}) {

   let navigate = useNavigate();
   let {title: titleParam} = useParams();
   let dispatch = useDispatch();
   let findproduct;
   
   console.log(product);
   console.log(perfume);
   console.log(titleParam);

   findproduct = product.find(function(x){
      console.log(x.title);
      return x.title == titleParam
   });

   if(!findproduct) {
      findproduct = perfume.find(function(x) {
         return x.title == titleParam;
      })
   }

   

   return(
      <>
         <Header navigate={navigate}/>

         <div className={styles.detail_main}>
            {findproduct && (
               <>
               <div className={styles.detail_img_area}>
                  <img src={findproduct.images} alt="Product"/>
               </div>
               <div className={styles.detail_product_text}>
                  <em>{findproduct.sub}</em>
                  <h1>{findproduct.title}</h1>
                  <h5>{findproduct.content}</h5>
                  <p>{findproduct.price.toLocaleString('ko-KR', { style: 'currency', currency: 'KRW' })}</p>
                  <button className={styles.add_cart_button} onClick={() => {
                     if (findproduct) {
                        dispatch(setProductDetail(findproduct))
                        alert('카트에 담겼습니다')
                     }else {
                        console.log('실패');
                     }
                  }}>
                     Add Cart
                  </button>
                  <div className={styles.product_detail}>
                     <ul>
                        <li>
                           <p>타입</p>
                           <span>{findproduct.type}</span>
                        </li>
                        <li>
                           <p>사용감</p>
                           <span>{findproduct.feeling}</span>
                        </li>
                        <li>
                           <p>주요 성분</p>
                           <span>{findproduct.ingredient}</span>
                        </li>
                     </ul>
                  </div>
               </div>
               </>
            )}
         </div>
      </>
   )
}

function Header({navigate}) {
   
   let [isMenuOpen, setIsMenuOpen] = useState(false);

   return (
     <div className={styles.nav_bar}>
         <div className={styles.nav_container}>
           <div className={styles.nav_menu}>
             <div className={styles.logo_area}>
               <img src={process.env.PUBLIC_URL + '/images/logo.png'} onClick={()=> { navigate('/')}} />
             </div>
             <ul>
               <li onClick={()=> { navigate('/skin')}}>SKIN</li>
               <li onClick={()=> { navigate('/body')}}>BODY</li>
               <li onClick={()=> { navigate('/hair')}}>HAIR</li>
               <li onClick={()=> { navigate('/perfume')}}>PERFUME</li>
             </ul>
           </div>
           <div className={styles.nav_login}>
             <ul>
               <li>로그인</li>
               <li><BiShoppingBag className={styles.nav_basket} onClick={() => {
                  navigate('/cart');
               }}/></li>
               <li><FaBars className={styles.nav_hamberger} onClick={() => {
                  setIsMenuOpen(true);
               }}/></li>
             </ul>
           </div>
         </div>
         {
          <div className={`toggle_container ${isMenuOpen ? 'toggle_show' : ''}`}>
            <IoCloseOutline className='close_button' onClick={()=> {
              setIsMenuOpen(false);
            }}/>
            <ul className='toggle_inner'>
              <li onClick={()=> { navigate('/skin')}}>SKIN</li>
              <li onClick={()=> { navigate('/body')}}>BODY</li>
              <li onClick={()=> { navigate('/hair')}}>HAIR</li>
              <li onClick={()=> { navigate('/perfume')}}>PERFUME</li>
            </ul>
          </div> 
        }
      </div>
   )
 }

export default Detail;